<template>
    <el-container>
        <el-header style="margin-top: 25px; text-align: left">
            <el-input
                    placeholder=""
                    v-model="enterpriseChangeToSearch.name" style="width: 350px; margin-right: 15px">
            </el-input>
            <el-button type="info" size="medium" style="margin-left: 15px" @click="search_direct"></el-button>
        </el-header>

        <el-main class="with_shadow">
            <el-table
                    ref="multipleTable"
                    :data="enterpriseChange"
                    tooltip-effect="dark"
                    style="width: 100%;"
                    max-height="510">

                <el-table-column
                        prop="enterpriseInformationChangeId"
                        label="id"
                        width="100"
                        align="left">
                </el-table-column>

                <el-table-column
                        prop="enterpriseId"
                        label="id"
                        width="100"
                        align="left">
                </el-table-column>

                <el-table-column
                        prop="enterpriseName"
                        label=""
                        align="left">
                </el-table-column>

                <el-table-column
                        prop="informationChangeDate"
                        label=""
                        align="left">
                </el-table-column>

                <el-table-column
                        prop="informationChangeType"
                        label=""
                        align="left">
                </el-table-column>

                <el-table-column
                        prop="informationBefore"
                        label=""
                        align="left">
                </el-table-column>

                <el-table-column
                        prop="informationAfter"
                        label=""
                        align="left">
                </el-table-column>

                <el-table-column
                        prop="createDate"
                        label=""
                        align="left">
                </el-table-column>

            </el-table>
        </el-main>

    </el-container>
</template>

<script>
    export default {
        name: "EnterpriseChangeSearch",
        data() {
            return {
                enterpriseChange: [],
                enterpriseChangeToSearch: {
                    name: '',
                },
            }
        },
        methods: {
            search_direct() {
                if (this.enterpriseChangeToSearch.name === '') {
                    this.$message({type: 'error', message: ''});
                    return;
                }
                let _this = this;
                this.getRequest("/change/enterprise_name/" + _this.enterpriseChangeToSearch.name).then(resp => {
                    if (resp && resp.status === 200) {
                        if (resp.data.length === 0) {
                            _this.enterpriseChange = [];
                            _this.$message({type: 'error', message: ''});
                        } else if (resp.data.length < 999) {
                            _this.enterpriseChange = resp.data;
                        } else {
                            _this.enterpriseChange = [];
                            _this.$message({type: 'error', message: ' '});
                        }

                    }
                });
            },
        },
    }
</script>

