<template>
    <el-container>
        <el-header style="margin-top: 25px; text-align: left">
            <el-input
                    placeholder=""
                    v-model="staffToSearch.name" style="width: 350px; margin-right: 15px">
            </el-input>
            <el-button type="info" size="medium" style="margin-left: 15px" @click="search_direct"></el-button>
            <el-button type="info" size="medium" style="margin-left: 15px" @click="search_by_enterprise">
            </el-button>
        </el-header>

        <el-main class="with_shadow">
            <el-table
                    ref="multipleTable"
                    :data="staff"
                    tooltip-effect="dark"
                    style="width: 100%;"
                    max-height="510">

                <el-table-column
                        prop="staffId"
                        label="id"
                        width="90"
                        align="left">
                </el-table-column>

                <el-table-column
                        prop="staffName"
                        label=""
                        width="90"
                        align="left">
                </el-table-column>

                <el-table-column
                        prop="enterpriseName"
                        label=""
                        align="left">
                </el-table-column>

                <el-table-column
                        prop="owningEnterpriseNumber"
                        label=""
                        align="left">
                </el-table-column>

                <el-table-column
                        prop="position"
                        label=""
                        align="left">
                </el-table-column>

                <el-table-column label="" width="220" align="left">
                    <template slot-scope="scope">
                        <el-button size="small" type="danger" icon="el-icon-delete"
                                   @click="delete_staff_dialog(scope.row)">
                        </el-button>
                        <el-button size="small" type="warning" icon="el-icon-edit"
                                   @click="modify_staff_dialog(scope.row)">
                        </el-button>
                    </template>
                </el-table-column>

            </el-table>
        </el-main>

        <el-dialog title="" :visible.sync="dialogDeleteConfirmVisible">
            <el-button type="warning" @click="delete_staff"> </el-button>
            <el-button type="info" @click="dialogDeleteConfirmVisible = false"> </el-button>
        </el-dialog>

        <el-dialog title="" :visible.sync="dialogModifyVisible">
            <el-form>
                <el-form-item label=""></el-form-item>
                <el-form-item label=":">
                    <el-input
                            placeholder=""
                            v-model="staffToModify.staffName">
                    </el-input>
                </el-form-item>
                <el-form-item label=":">
                    <el-input
                            placeholder=""
                            v-model="staffToModify.owningEnterpriseNumber">
                    </el-input>
                </el-form-item>
                <el-form-item label=":">
                    <el-input
                            placeholder=""
                            v-model="staffToModify.position">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="warning" @click="modify_staff"> </el-button>
                <el-button type="info" @click="dialogModifyVisible = false"> </el-button>
            </div>
        </el-dialog>

    </el-container>
</template>

<script>
    export default {
        name: "StaffSearch",
        data() {
            return {
                staff: [],
                dialogDeleteConfirmVisible: false,
                dialogModifyVisible: false,
                staffToSearch: {
                    name: '',
                    by_name: false,
                },
                originalStaff: {
                    staffId: '',
                    enterpriseId: 0,
                    staffName: '',
                    position: '',
                },
                staffToModify: {
                    staffId: '',
                    staffName: '',
                    owningEnterpriseNumber: 0,
                    position: '',
                },
            }
        },
        methods: {
            search_direct() {
                if (this.staffToSearch.name === '') {
                    this.$message({type: 'error', message: ''});
                    return;
                }
                this.staffToSearch.by_name = true;
                let _this = this;
                this.getRequest("/staff/name/" + _this.staffToSearch.name).then(resp => {
                    if (resp && resp.status === 200) {
                        if (resp.data.length === 0) {
                            _this.staff = [];
                            _this.$message({type: 'error', message: ''});
                        } else if (resp.data.length < 999) {
                            _this.staff = resp.data;
                        } else {
                            _this.staff = [];
                            _this.$message({type: 'error', message: ' '});
                        }

                    }
                });
            },
            search_by_enterprise() {
                if (this.staffToSearch.name === '') {
                    this.$message({type: 'error', message: ''});
                    return;
                }
                this.staffToSearch.by_name = false;
                let _this = this;
                this.getRequest("/staff/enterprise/" + _this.staffToSearch.name).then(resp => {
                    if (resp && resp.status === 200) {
                        if (resp.data.length === 0) {
                            _this.staff = [];
                            _this.$message({type: 'error', message: ''});
                        } else if (resp.data.length < 1999) {
                            _this.staff = resp.data;
                        } else {
                            _this.staff = [];
                            _this.$message({type: 'error', message: ' '});
                        }
                    }
                });
            },
            delete_staff_dialog(row) {
                this.originalStaff.staffId = row.staffId;
                this.dialogDeleteConfirmVisible = true;
            },
            delete_staff() {
                let _this = this;
                this.postRequest("/staff/delete", {"staffId": _this.originalStaff.staffId}).then(resp => {
                    if (resp && resp.status === 200) {
                        if (this.staffToSearch.by_name === true) {
                            _this.search_direct();
                        } else {
                            _this.search_by_enterprise();
                        }
                        _this.dialogDeleteConfirmVisible = false;
                        _this.$message({type: 'success', message: ''});
                    } else {
                        _this.dialogDeleteConfirmVisible = false;
                        _this.$message({type: 'error', message: ''});
                    }
                });
            },
            modify_staff_dialog(row) {
                this.originalStaff.staffId = row.staffId;
                this.originalStaff.enterpriseId = row.enterpriseId;
                this.originalStaff.staffName = row.staffName;
                this.originalStaff.position = row.position;
                this.staffToModify.staffId = row.staffId;
                this.staffToModify.staffName = row.staffName;
                this.staffToModify.owningEnterpriseNumber = row.owningEnterpriseNumber;
                this.staffToModify.position = row.position;
                this.dialogModifyVisible = true;
            },
            modify_staff() {
                let _this = this;
                this.postRequest("/staff/modify", {
                    "staffId": _this.staffToModify.staffId,
                    "staffName": _this.staffToModify.staffName,
                    "owningEnterpriseNumber": _this.staffToModify.owningEnterpriseNumber,
                    "position": _this.staffToModify.position
                }).then(resp => {
                    if (resp && resp.status === 200) {
                        if (this.staffToSearch.by_name === true) {
                            _this.search_direct();
                        } else {
                            _this.search_by_enterprise();
                        }
                        _this.dialogModifyVisible = false;
                        _this.$message({type: 'success', message: ''});
                    } else {
                        _this.dialogModifyVisible = false;
                        _this.$message({type: 'error', message: ''});
                    }
                });
            },
        },
    }
</script>

